import {
  firstGraphDataJoined,
  secondGraphDataJoined,
  thirdGraphDataJoined,
  fourthGraphDataJoined,
  fifthGraphDataJoined,
  firstGraphDataLeft,
  secondGraphDataLeft,
  thirdGraphDataLeft,
  fourthGraphDataLeft,
  fifthGraphDataLeft,
  firstGraphXAxisMin,
  firstGraphXAxisMax,
  secondGraphXAxisMin,
  secondGraphXAxisMax,
  thirdGraphXAxisMin,
  thirdGraphXAxisMax,
  fourthGraphXAxisMin,
  fourthGraphXAxisMax,
  fifthGraphXAxisMin,
  fifthGraphXAxisMax,
  firstGraphDataJoinedMin,
  firstGraphDataJoinedMax,
  secondGraphDataJoinedMin,
  secondGraphDataJoinedMax,
  thirdGraphDataJoinedMin,
  thirdGraphDataJoinedMax,
  fourthGraphDataJoinedMin,
  fourthGraphDataJoinedMax,
  fifthGraphDataJoinedMin,
  fifthGraphDataJoinedMax,
  firstGraphDataLeftMin,
  firstGraphDataLeftMax,
  secondGraphDataLeftMin,
  secondGraphDataLeftMax,
  thirdGraphDataLeftMin,
  thirdGraphDataLeftMax,
  fourthGraphDataLeftMin,
  fourthGraphDataLeftMax,
  fifthGraphDataLeftMin,
  fifthGraphDataLeftMax,
} from '../data/data-handler';
import getNormalizedData from '../data/data-normalization';

const graphAreaWidth = 800;
const graphAreaHeight = 500;

const mainGraphArea = document.getElementById('main-graph-area');
mainGraphArea.setAttributeNS(null, 'width', graphAreaWidth);
mainGraphArea.setAttributeNS(null, 'height', graphAreaHeight);

// ???
const xPointsArr = [];

firstGraphDataJoined.forEach((item, i) => {
  const xAxis = getNormalizedData(i, firstGraphXAxisMin, firstGraphXAxisMax, 0, graphAreaWidth);
  xPointsArr.push(xAxis);
});

// ???
const yPointsArr = [];

firstGraphDataJoined.forEach((item, i) => {
  const yPoints = getNormalizedData(firstGraphDataJoined[i][1], firstGraphDataJoinedMin, firstGraphDataJoinedMax, 0, graphAreaHeight);
  yPointsArr.push(yPoints);
});

const firstGraph = firstGraphDataJoined.forEach((item, i) => {
  const xPoints = getNormalizedData(i, firstGraphXAxisMin, firstGraphXAxisMax, 0, graphAreaWidth);
  const yPoints = getNormalizedData(firstGraphDataJoined[i][1], firstGraphDataJoinedMin, firstGraphDataJoinedMax, 0, graphAreaHeight);
  console.log(typeof xPoints);
  
  // Tips of joined followers
  const tipJoined = document.createElementNS('http://www.w3.org/2000/svg', 'circle');
  // tipJoined.setAttributeNS(null, 'cx', xPoints);
  // tipJoined.setAttributeNS(null, 'cy', yPoints);
  tipJoined.setAttributeNS(null, 'cx', xPointsArr[i]);
  tipJoined.setAttributeNS(null, 'cy', yPointsArr[i]);
  tipJoined.setAttributeNS(null, 'r', '3');
  tipJoined.setAttributeNS(null, 'stroke', 'green');
  tipJoined.setAttributeNS(null, 'stroke-width', '3');
  tipJoined.setAttributeNS(null, 'transform', 'translate(0, 490) scale(1, -1)');
  mainGraphArea.appendChild(tipJoined);

  // Graph of joined followers
  const linePath = document.createElementNS('http://www.w3.org/2000/svg', 'path');
  linePath.setAttributeNS(null, 'd', `M${xPointsArr[i]} ${yPointsArr[i]} L${xPointsArr[i + 1]} ${yPointsArr[i + 1]}`);
  linePath.setAttributeNS(null, 'stroke', 'green');
  linePath.setAttributeNS(null, 'stroke-width', '2');
  linePath.setAttributeNS(null, 'stroke-linecap', 'square');
  linePath.setAttributeNS(null, 'stroke-linejoin', 'miter');
  linePath.setAttributeNS(null, 'fill', 'none');
  linePath.setAttributeNS(null, 'transform', 'translate(0, 490) scale(1, -1)');

  mainGraphArea.appendChild(linePath);
});

export default firstGraph;
