/**
 * Sorting numbers
 * @param {Number} a
 * @param {Number} b
 */
export const getSortedNumbers = (a, b) => a - b;

/**
 *
 * @param {Object[]} matrix
 * @param {Number} elemNumber
 */
export const getSortedMatrixItems = (matrix, elemNumber) => {
  const sortedValues = [];

  for (let i = 0; i < matrix.length; i += 1) {
    const value = matrix[i][elemNumber];
    sortedValues.push(value);
    sortedValues.sort(getSortedNumbers);
  }
  return sortedValues;
};
